class Pin {

    private _state : number = 0;
    private _mode : boolean = false;
    private _type : boolean = false;

    get state(): number {
        return this._state;
    }

    set state(value: number) {
        this._state = value;
    }

    get mode(): boolean {
        return this._mode;
    }

    set mode(value: boolean) {
        this._mode = value;
    }

}

export default Pin;